.app {
  position: relative;
  margin: auto;
  min-width: 360px;
  max-width: 1920px;
  height: 100vh;
  color: #242b43;
  background-color: #f7f7f7;
  transition: color 0.25s;
  transition: background-color 0.25s;
  overflow: hidden;
}

.app[dark-theme] {
  color: #efe9ea;
  background-color: #313a58;
}

.content {
  position: relative;
}

.section {
  height: calc(100vh - 64px);
  overflow-y: auto;
}
