%link {
  font-size: 0;
}

.header {
  position: relative;
  height: 64px;
  padding: 16px 24px;
  color: inherit;
  background-color: #ef336a;
  box-shadow: 0px 3px 6px #00000029;
  z-index: 1;
}

.rightSide {
  float: right;
  height: 100%;
  display: flex;
  align-items: center;
}

@media (max-width: 991.98px) {
  .logoLink {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
}

.logoIcon {
  height: 32px;
  color: #efe9ea;
}

.themeIcon {
  cursor: pointer;
}

.profileLink {
  @extend %link;
  margin-left: 16px;
}

.profileIcon {
  height: 32px;
  color: #efe9ea;
}
