.competitivenessSpeedometer {
  display: flex;
  flex-direction: column;
  max-width: 680px;
}

.labelsWrapper {
  display: flex;
  justify-content: flex-end;
}

.labels {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .labels {
    width: 100%;
    flex-direction: row;
    justify-content: center;
  }
}

.label {
  padding: 4px 8px;
  display: flex;
  align-items: center;
  cursor: pointer;
  & > div {
    &:nth-child(1) {
      flex-shrink: 0;
      margin-right: 4px;
      width: 16px;
      height: 16px;
      border-radius: 50%;
    }
    &:nth-child(2) {
      font-size: 10px;
      font-weight: 700;
      color: #efe9ea;
      overflow: hidden;
      white-space: nowrap;
      text-align: right;
      text-overflow: ellipsis;
    }
  }
  &Inactive {
    opacity: 0.25;
  }
}

@media (max-width: 767.99px) {
  .label {
    max-width: 100% !important;
  }
}

.widgetWrapperOuter {
  position: relative;
  padding-bottom: 78.82%;
}

.widgetWrapperInner {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.widget {
  position: absolute;
  width: 680px;
  height: 536px;
}

.center {
  position: absolute;
  left: 50%;
  top: calc(2 / 3 * (100% - 24px));
}

.curvedLetter {
  position: absolute;
  left: -8px;
  top: -8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  font-size: 13px;
  font-weight: 400;
  text-transform: uppercase;
  writing-mode: vertical-rl;
  & > div {
    transform: rotate(180deg);
  }
}

.mainTick {
  position: absolute;
  left: -16px;
  top: -16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 18px;
  font-weight: 700;
  color: #efe9ea;
}

.tick {
  position: absolute;
  left: -16px;
  top: -16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 10px;
  font-weight: 900;
  color: #20263b;
  border-radius: 50%;
}

.roundPointer {
  position: absolute;
  display: flex;
  align-items: center;
  border-width: 2px;
  border-style: solid;
  border-radius: 50%;
  & > div {
    &:nth-child(1) {
      width: 100%;
      height: 100%;
      border-style: solid;
      border-radius: 50%;
      opacity: 0.5;
    }
    &:nth-child(2) {
      position: absolute;
      padding: 4px;
      color: #efe9ea;
      font-size: 13px;
      font-weight: 500;
    }
    &:nth-child(3) {
      position: absolute;
      height: 1px;
    }
    &:nth-child(4) {
      position: absolute;
      width: 16px;
      height: 16px;
      border-radius: 50%;
    }
  }
}

.arrowPointer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: all 1s ease;
  pointer-events: none;
  & > div {
    transition: inherit;
    &:nth-child(1) {
      color: #efe9ea;
      font-size: 20px;
      font-weight: 500;
    }
    &:nth-child(2) {
      position: absolute;
      border-left: none;
      border-right-style: solid;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
    }
  }
}

.message {
  align-self: center;
  padding: 2px 8px;
  font-size: 12px;
  color: rgb(206, 206, 132);
  text-transform: uppercase;
  white-space: nowrap;
  border: 1px solid #4f80e3;
}

.explanation {
  align-self: center;
  margin-top: 16px;
  width: 100%;
  max-width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
  font-weight: 400;
  text-transform: uppercase;
  & > div {
    &:nth-child(1) {
      color: #ef336a;
    }
    &:nth-child(2),
    &:nth-child(4) {
      flex: 1;
      margin: 0 16px;
      border-top: 1px solid #707070;
    }
    &:nth-child(5) {
      color: #41b167;
    }
  }
}

@media (min-width: 768px) {
  .label {
    & > div {
      &:nth-child(1) {
        max-width: 88px;
      }
    }
  }
  .message {
    font-size: 14px;
  }
}
