.requestServeyModal {
  position: relative;
  width: 296px;
  padding: 32px 24px 24px;
  background-color: #20263b;
}

.closeIcon {
  position: absolute;
  right: 0;
  top: 0;
}

.content {
  display: grid;
  grid-template:
    "textWrapper textWrapper" auto
    "imageWrapper imageWrapper" auto
    "redButtonWrapper greenButtonWrapper" auto / 1fr 1fr;
  grid-gap: 24px;
}

.textWrapper {
  grid-area: textWrapper;
  display: flex;
  justify-content: center;
  align-items: center;
}

.text {
  &Main {
    color: #cece84;
    font-size: 16px;
  }
  &Secondary {
    font-size: 12px;
  }
}

.imageWrapper {
  grid-area: imageWrapper;
}

.image {
  margin: auto;
  display: block;
  width: 80%;
}

.redButtonWrapper {
  grid-area: redButtonWrapper;
}

.greenButtonWrapper {
  grid-area: greenButtonWrapper;
}

.button {
  width: 100%;
  height: 56px;
  border-radius: 4px;
  font-size: 11px;
  font-weight: 500;
  &Red {
    color: #ef336a;
    border: 1px solid #ef336a;
  }
  &Green {
    color: #20263b;
    background-color: #41b167;
  }
}

@media (min-width: 768px) {
  .requestServeyModal {
    width: 704px;
  }
  .content {
    grid-template:
      "textWrapper imageWrapper" auto
      "redButtonWrapper greenButtonWrapper" auto / 1fr 1fr;
  }
  .text {
    &Main {
      font-size: 20px;
    }
    &Secondary {
      font-size: 14px;
    }
  }
  .button {
    font-size: 14px;
  }
}
