.brandSelector {
  position: relative;
  width: 160px;
  font-size: 12px;
  color: #242b43;
  cursor: pointer;
}

.label {
  position: absolute;
  left: 8px;
  top: -12px;
  color: #cece84;
  font-size: 8px;
  text-transform: uppercase;
}

.input {
  display: flex;
  background-color: #efefef;
  border-radius: 4px;
}

.open {
  .input {
    border-radius: 4px 4px 0 0;
  }
}

.arrow {
  position: relative;
  width: 24px;
  transition: all 0.2s;
  &::after {
    content: "";
    position: absolute;
    top: calc(50% - 2px);
    left: 50%;
    transform: translate(-50%, -50%) rotate(-135deg);
    width: 6px;
    height: 6px;
    border-left: 1px solid #242b43;
    border-top: 1px solid #242b43;
  }
}

.open {
  .arrow {
    transform: rotate(180deg);
  }
}

.options {
  position: absolute;
  left: 0px;
  right: 0px;
  background-color: #efefef;
  border-radius: 0 0 4px 4px;
  overflow: hidden;
  z-index: 2;
}

.option {
  padding: 0 8px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.input {
  .option {
    flex: 1;
    font-weight: 500;
    height: 24px;
    line-height: 24px;
  }
}

.options {
  .option {
    height: 20px;
    line-height: 20px;
    border-top: 1px solid #242b43;
    &:hover {
      background-color: #d1d1d1;
    }
  }
}
