.competitivenessTable {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 296px;
  color: #efe9ea;
}

.title {
  width: 100%;
  height: 28px;
  line-height: 28px;
  font-size: 16px;
  text-transform: uppercase;
  border-bottom: 1px solid rgba($color: #707070, $alpha: 0.25);
}

.headRow {
  margin-top: 40px;
  display: flex;
}

.headCell {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 32px;
  font-size: 8px;
  text-align: center;
}

.body {
  position: relative;
  font-size: 14px;
  font-weight: 700;
}

.bodyRow {
  position: relative;
  margin-top: 24px;
  display: flex;
  align-items: center;
}

.arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;

  font-size: 24px;
  font-weight: 300;

  &Up {
    color: rgb(65, 177, 103);
    transform: rotate(-90deg);
  }
  &Down {
    padding-bottom: 6px;
    color: rgb(239, 51, 106);
    transform: rotate(90deg);
  }
}

.number {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.brand {
  margin: 0 12px;
  width: 112px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.value {
  width: 36px;
  text-align: center;
}

%divider {
  position: absolute;
  opacity: 0.25;
}

.divider {
  &Horizontal {
    @extend %divider;
    right: 0;
    bottom: 0;
    width: 260px;
    height: 1px;
  }
  &Vertical {
    @extend %divider;
    background-color: #707070;
    top: 0;
    width: 1px;
    height: 100%;
  }
}
