.basicModal {
  font-size: 18px;
}

.buttonsWrapper {
  display: flex;
  justify-content: center;
  .button {
    margin-top: 24px;
    min-width: 112px;
    height: 32px;
    padding: 0 16px;
    font-size: 16px;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    &:nth-child(2) {
      margin-left: 24px;
    }
  }
}

@media (min-width: 768px) {
  .buttonsWrapper {
    .button {
      &:nth-child(2) {
        margin-left: 40px;
      }
    }
  }
}
