.signup {
  width: 100%;
  max-width: 568px;
  padding: 24px;
}

.logo {
  &Wrapper {
    text-align: center;
  }
  height: 100px;
}

.title {
  margin-top: 16px;
  font-size: 18px;
  text-align: center;
}

.note1 {
  margin-top: 8px;
  text-align: center;
  font-size: 12px;
  opacity: 0.6;
}

.inputsWrapper {
  margin-top: 16px;
  display: grid;
  grid-gap: 16px;
  grid-template:
    "a b" 40px
    "c c" 40px
    "d e" 40px / auto;

  & > * {
    &:nth-child(1) {
      grid-area: a;
    }
    &:nth-child(2) {
      grid-area: b;
    }
    &:nth-child(3) {
      grid-area: c;
    }
    &:nth-child(4) {
      grid-area: d;
    }
    &:nth-child(5) {
      grid-area: e;
    }
  }
}

.button {
  margin-top: 16px;
  width: 100%;
  height: 40px;
  font-size: 15px;
  font-weight: 400;
  border-radius: 4px;
  &Green {
    // background-color: rgba(49, 88, 69, 1);
    color: #f7f7f7;
    background-color: #41b167;
  }
  &Blue {
    // background-color: #3a509b;
    border: 1px solid #3a509b;
  }
}

.note2 {
  margin-top: 8px;
  font-size: 11px;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .logo {
    height: 174px;
  }
  .title {
    margin-top: 32px;
    font-size: 32px;
  }
  .note1 {
    font-size: 14px;
  }
  .inputsWrapper {
    margin-top: 24px;
    grid-gap: 24px;
  }
  .button {
    margin-top: 32px;
  }
}
