.verticalFunnelChart {
  display: flex;
  flex-direction: column;
  height: 368px;
  padding: 4px 0;
}

.rowWrapper {
  flex: 1;
  position: relative;
  border-bottom: 1px solid rgba(239, 233, 234, 0.15);
}

.row {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.shapeWrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  opacity: 0.75;
}

.labelWrapper {
  position: absolute;
  left: 0;
  // max-width: 50%;
  max-width: 100%;
  display: flex;
  align-items: center;
  div {
    &:nth-child(1) {
      margin-right: 8px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 28px;
      height: 28px;
      border-radius: 50%;
      font-size: 10px;
      font-weight: 500;
      color: rgb(239, 233, 234);
    }
    &:nth-child(2) {
      max-width: calc(100% - 36px);
      white-space: nowrap;
      overflow: hidden;
      text-transform: uppercase;
      text-overflow: ellipsis;
      font-size: 10px;
      font-weight: 700;
      color: rgb(239, 233, 234);
    }
  }
}

