.withError {
  position: relative;
}

.error {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: 2px solid rgb(239, 51, 106);
  border-radius: 4px;
  pointer-events: none;

  &::after {
    content: attr(data-error);
    position: absolute;
    height: 16px;
    line-height: 16px;
    font-size: 11px;
    bottom: -16px;
    color: rgb(239, 51, 106);
  }
}
