.userButton {
  position: relative;
  border: 1px solid #ef336a;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}

.dropdown {
  position: absolute;
  top: 40px;
  right: -20px;
  width: 160px;
}

.items {
  position: absolute;
  left: 1px;
  top: 16px;
  width: calc(100% - 2px);
}

.item {
  height: 36px;
  display: flex;
  align-items: center;
  font-size: 16px;
  padding: 0 15px;
  color: #fdfdfd;
  background-color: #37477c;
  &:hover {
    background-color: #ef336a;
  }
}

.poly {
  stroke: #ef336a;
  stroke-width: 1;
}
