.attitudePieChart {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.labels {
  display: flex;
  justify-content: center;
  height: 24px;
}

.label {
  display: flex;
  align-items: center;
  color: #efe9ea;
  font-size: 10px;
  &:not(:last-child) {
    margin-right: 16px;
  }
}

.labelIcon {
  margin-right: 4px;
  border-radius: 50%;
  width: 16px;
  height: 16px;
}

.chartsWrapper {
  flex: 1;
  margin: 16px 0 24px;
  display: grid;
  grid-gap: 40px;
  &For1 {
    grid-template: "chart0" auto/ auto;
  }
  &For3 {
    grid-template:
      "chart0 chart0" auto
      "chart1 chart2" auto / auto auto;
  }
  &For5 {
    grid-template:
      "chart0 chart0" auto
      "chart1 chart2" auto
      "chart3 chart4" auto / auto auto;
  }
}

.brandName {
  margin-top: 4px;
  display: flex;
  justify-content: center;
  height: 16px;
  color: #efe9ea;
  font-size: 12px;
  white-space: nowrap;
}

@media (max-width: 767.99px) {
  .chartWrapper {
    &:nth-child(1) {
      width: 296px !important;
      height: 296px !important;
    }
    &:not(:nth-child(1)) {
      width: 128px !important;
      height: 128px !important;
    }
  }
}

@media (min-width: 768px) {
  .chartsWrapper {
    &For3 {
      grid-template:
        "chart0 chart1" auto
        "chart0 chart2" auto / auto auto;
    }
    &For5 {
      grid-template:
        "chart0 chart0 chart1" auto
        "chart0 chart0 chart2" auto
        "chart3 chart4 placeholder" auto / auto auto auto;
    }
  }
}

@media (min-width: 992px) {
  .chartsWrapper {
    &For5 {
      grid-template:
        "chart0 chart1 chart3" auto
        "chart0 chart2 chart4" auto / auto auto auto;
    }
  }
}
